light: rgba(0,0,0,0.8);\n$bckColor-light: #FFFFFF;\n\n\n\n// General Styling\n*{\n    margin: 0;\n    padding: 0;\n    box-sizing: border-box;\n    font-family: 'Ubuntu', sans-serif;\n}\na {\n    text-decoration: none;\n    color: inherit;\n}\ninput {\n    -webkit-appearance: none;\n    -moz-appearance: none;\n    appearance: none;\n}\n\n@import 'header';\n@import 'login';\n@import 'booklist';\n@import 'loader';\n\n.app{\n    min-height: 100vh;\n    .container{\n        width: 90%;\n        margin: 20px auto 0;\n        padding-bottom: 20px;\n    }\n    &.dark{\n        color:#FFFFFF;\n        background-color: $bckColor-dark;\n        @include header($box-shadow-color-dark);\n        @include loginPage($color-dark, $bckColor-dark, $box-shadow-color-dark);\n        @include booklist($color-dark, $bckColor-dark, $box-shadow-color-dark);\n        @include loader($color-dark);\n        .footer{\n            width: 100%;\n            text-align: center;\n            padding: 10px 0;\n            color: $color-dark;\n        }\n    }\n    &.light{\n        color:#000000;\n        background-color: $bckColor-light;\n        @include header($box-shadow-color-light);\n        @include loginPage($color-light, $bckColor-light, $box-shadow-color-light);\n        @include booklist($color-light, $bckColor-light, $box-shadow-color-light);\n        @include loader($color-light);\n        .footer{\n            width: 100%;\n            text-align: center;\n            padding: 10px 0;\n            color: $color-light;\n        }\n    }\n}\n","@mixin header ($shadow-color){\n    .head{\n        width: 100%;\n        height: 60px;\n        box-shadow: 0 1px 5px $shadow-color;\n        display: flex;\n        justify-content: space-between;\n        padding: 5px 5%;\n        .branding{\n            display: flex;\n            text-align: center;\n            justify-content: flex-start;\n            align-items: center;\n            &_title{\n                margin: 0 15px;\n                font-size: 20px;\n                font-weight: bold;\n            }\n        }\n        .navigation{\n            display: flex;\n            justify-content: flex-start;\n            align-items: center;\n            > a:not(:last-of-type) {\n                margin-right: 20px;\n            }\n            a{\n                cursor: pointer;\n                &.theme-icon{\n                    width: 30px;\n                    text-align: center;\n                }\n                .auth-icon{\n                    margin-right: 5px;\n                }\n            }\n        }\n        @media only screen and (max-width: 500px) {\n            .navigation{\n                a{\n                    font-size: 14px;\n                }\n            }\n        }\n    }\n}\n","@mixin loginPage($color, $bckColor, $shadow-color) {\n    .login-screen{\n        margin: 20px auto;\n        min-width: 200px;\n        max-width: 300px;\n        position: relative;\n        top: 100px;\n        .branding{\n            width: 100%;\n            display: flex;\n            justify-content: center;\n            align-items: center;\n            flex-direction: column;\n            margin-bottom: 80px;\n            > span{\n                width: fit-content;\n            }\n            &_title{\n                font-size: 30px;\n                margin-top: 20px;\n            }\n            &_tagline{\n                font-size: 14px;\n            }\n        }\n        form{\n            display: flex;\n            flex-direction: column;\n            justify-content: center;\n            align-items: stretch;\n            .error{\n                text-align: center;\n                color: red;\n            }\n            label{\n                margin-right: 10px\n            }\n            input:not([type=\"submit\"]){\n                padding: 8px 0;\n                font-size: 16px;\n                display: block;\n                margin-bottom: 10px;\n                border:none;\n                border-bottom: 2px solid $shadow-color;\n                background-color: transparent;\n                color: $color;\n                outline: none;\n                &:focus{\n                    border-bottom: 2px solid $primary;\n                }\n            }\n            .submit-button{\n                margin-top: 20px;\n                padding: 10px 15px;\n                border-radius: 4px;\n                font-size: 18px;\n                cursor: pointer;\n                background-color: $primary;\n                color: #FFFFFF;\n                border: none;\n                transition: all 0.2s ease;\n                min-height: 20px;\n                input{\n                    all: inherit;\n                    background: transparent;\n                    width: 100%;\n                    text-align: center;\n                    padding: 0;\n                    margin:0;\n                }\n                &:hover{\n                    background-color: $primaryHover;\n                }\n            }\n        }\n    }\n}","@mixin bookcard($color, $bckColor, $shadowColor){\n    .book{\n        margin: 0 8px 20px 8px;\n        width: 200px;\n        height: 300px;\n        border-radius: 4px;\n        box-shadow: 0 1px 10px $shadowColor;\n        background: $bckColor;\n        cursor: default;\n        transition: 0.2s;\n        overflow: hidden;\n        position: relative;\n        &_image{\n            img{\n                width: 100%;\n            }\n        }\n        &_description{\n            width: 100%;\n            position: absolute;\n            bottom: 0;\n            padding: 100px 10px 10px;\n            font-size: 14px;\n            background: rgb(2,0,36);\n            background: linear-gradient(0deg, black, transparent);\n            color: #ffffff;\n            letter-spacing: 0.015em;\n            .category{\n                display: block;\n                width: fit-content;\n                font-size: 10px;\n                padding: 2px 5px;\n                background-color: red;\n                color:#ffffff;\n                margin-bottom: 3px;\n                box-shadow: 0 2px 5px rgba(0,0,0,0.25);\n            }\n            .author{\n                font-size: 11px;\n                display: block;\n                color: rgba(255,255,255, 0.7);\n                margin-top: 3px;\n                .author-icon{\n                    margin-right: 3px;\n                }\n            }\n        }\n        .view-details{\n            display: none;\n            width: fit-content;\n            padding:8px 10px;\n            color: #ffffff;\n            background-color: $primary;\n            position: absolute;\n            border-radius: 4px;\n            border: 1px solid $primary;\n            filter: blur(0px) !important;\n            filter: blur(0px) !important;\n            top: calc(50% - 10px );\n            left: calc(50% - 50px);\n            transition: all 0.6s ease;\n        }\n        &:hover{\n            z-index: 99;\n            transform: scale(1.2, 1.2);\n            font-size: 15px;\n            box-shadow: 2px 2px 10px $shadowColor, -2px -2px 10px $shadowColor;\n            &_title{\n                display: none;\n            }\n            // > * {\n            //     filter: blur(4px);\n            // }\n            // .view-details{\n            //     display: block;\n            // }\n            .actions{\n                display: block;\n            }\n        }\n        .actions{\n            display: none;\n            position: absolute;\n            top: 5px;\n            right: 5px;\n            border-radius: 4px;\n            cursor: pointer;\n            background: $bckColor;\n            color: $color;\n            padding: 2px 4px;\n            font-size: 14px;\n            .pencil-icon{\n                margin-right: 10px;\n            }\n        }\n        @media only screen and (max-width: 500px) {\n       